import React, { Component } from "react";
import { withStyles, createStyles, TextField } from "@material-ui/core";
import Cookies from "js-cookie";
import { Button } from "@material-ui/core";
import { DropzoneArea } from "material-ui-dropzone";
import Screen from "./Screen";
import { uploadImage } from "../utils/s3-meetable";
import FlexDiv from "../design-system/FlexDiv";
import { useRef } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import { BASE_URL } from "../utils/api";
const SocialText = withStyles({
  root: {
    marginBottom: "10px",
    backgroundColor: "#FEAC00",
    "& label.Mui-focused": {
      color: "white",
    },
  },
})(TextField);

const MakeSocial = (props) => {
  const [uid, setUid] = useState()
  const [form, setForm] = useState({
    instagram: "",
    snapchat: "",
    facebook: "",
    bio: "",
    bioError: false,
    textError: false,
    imageError: false,
    image: [],
  });
  const history = useHistory();
  const [qid, setQid] = useState();

  const formRef = useRef();
  useEffect(() => {
    console.log(props);

    setQid(Cookies.get("quizId"));

    const u = props.meetableId ? props.meetableId : props.match.params.id;
    setUid(u)
    const f = async () => {
      let res = await fetch(`${BASE_URL}/api/users/getById/${u}`, {
        method: "get",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      let json = await res.json();
      let user = json.user;
      console.log(json);
      setForm({
        ...form,
        facebook: user.facebook,
        instagram: user.instagram,
        snapchat: user.snapchat,
        bio: user.blurb,
        s3URL: user.avatar,
      });
    };
    f();
  }, [props]);
  const checkErrors = () => {
    if (!form.instagram && !form.facebook && !form.snapchat) {
      setForm({ ...form, textError: true });
    }
    if (!form.image === []) {
      setForm({ ...form, imageError: true });
    }
    if (form.bio.length < 20 || form.bio.length >= 300) {
      setForm({ ...form, bioError: true });
    }

    if (
      (!form.instagram && !form.facebook && !form.snapchat) ||
      !form.image === [] ||
      form.bio.length < 20 ||
      form.bio.length >= 300
    )
      return false;
    return true;
  };

  const handleImageDropZone = (files) => {
    uploadImage(files[0]).then((res) => {
      if (res) {
        if (res.status == 200) {
          const s3URL = res.body;
          console.log(s3URL);
          console.log(s3URL.location);
          setForm({ ...form, s3URL });
        }
      }
    });
  };

  const answer = async () => {
    if (!uid) return;

    let res = await fetch(`${BASE_URL}/api/users/updateprofile`, {
      method: "put",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        uid,
        avatar: form.s3URL,
        blurb: form.bio,
        instagram: form.instagram,
        snapchat: form.snapchat,
        facebook: form.facebook,
      }),
    });
    console.log(form);

    let json = await res.json();
    console.log(json);
    const user = json.user;

    //update responses with uid
    console.log("asdf");
    const body = JSON.stringify({
      uid,
      qid,
    })
    if (!user.groups.length) {
      console.log(body);
      let res2 = await fetch(`${BASE_URL}/api/quizs/byid`, {
        method: "put",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body,
      });
      console.log(form);

      let json = await res2.json();
      console.log(json);
      const uid = props.meetableId ? props.meetableId : props.match.params.id;
      history.push(`/groups/${uid}`);
    }
  };

  return (
    <Screen>
      <div style={{ padding: 16 }}>
        <h2 style={{ fontFamily: "Raleway", marginBottom: 0 }}>
          Almost there!
        </h2>
        <div>
          <h3>Customize your bio</h3>
          <p>This will be visible to your friend group</p>
        </div>
        <FlexDiv vert>
          <p>Snapchat</p>
          <SocialText
            value={form.snapchat}
            onChange={(e) => {
              setForm({ ...form, snapchat: e.target.value });
              console.log(form);
            }}
          />
          <p>Instagram</p>
          <SocialText
            value={form.instagram}
            onChange={(e) => {
              setForm({ ...form, instagram: e.target.value });
              console.log(form);
            }}
          />
          <p>Facebook</p>
          <SocialText
            value={form.facebook}
            onChange={(e) => {
              setForm({ ...form, facebook: e.target.value });
              console.log(form);
            }}
          />
          <p>Introduce yourself! (min 50 chars.)</p>
          <SocialText
            value={form.bio}
            onChange={(e) => {
              setForm({ ...form, bio: e.target.value });
              console.log(form);
            }}
          />
        </FlexDiv>
      </div>
      <FlexDiv container style={{ padding: 16 }}>
        <DropzoneArea
          filesLimit={1}
          acceptedFiles={["image/*"]}
          dropzoneText={"Upload a profile picture! *required* "}
          useChipsForPreview
          onChange={handleImageDropZone}
        />
        {form.imageError && <p>Please upload a photo</p>}
        {form.s3URL && <img src={form.s3URL} style={{ width: "33%" }} />}
      </FlexDiv>

      <StyledButton
        onClick={() => {

        
            answer();
            history.push("/");
  
        }}
        style={{ marginTop: 32 }}
      >
        Save and continue
      </StyledButton>
    </Screen>
  );
};

const StyledButton = withStyles({
  root: {
    background: "linear-gradient(45deg, #45CAED 30%, #02A3F4 90%)",
    borderRadius: 3,
    border: 0,
    color: "white",
    height: 25,
    width: 300,
    padding: "0 30px",
    margin: "10px",
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
  },
  label: {
    fontFamily: "Raleway",
  },
})(Button);
const muiStyles = createStyles({});
export default withStyles(muiStyles)(MakeSocial);
