import React, { Component } from "react";
import background from "../assets/background.png";
import { withStyles, createStyles, Button } from "@material-ui/core";
import LoginButton from "./LoginButton";
import { useEffect } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import FlexDiv from "../design-system/FlexDiv";
import Cookies from "js-cookie";
import Screen from "./Screen";
import MakeSocial from "./MakeSocial";
const HomePage = (props) => {
  const [user, setUser] = useState();
  const history = useHistory();
  const [openSocialPrompt, setOpenSocialPrompt] = useState(false)
  const [qid, setQid] = useState()
  const StyledButton = withStyles({
    root: {
      background: "linear-gradient(45deg, #45CAED 30%, #02A3F4 90%)",
      borderRadius: 3,
      border: 0,
      color: "white",
      height: 48,
      padding: "0 30px",
      boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
      ...props.style,
    },
    label: {
      fontFamily: "Raleway",
    },
  })(Button);
  useEffect(() => {
    const quizId = Cookies.get("qid")
    if(quizId) {
      setQid(quizId)
    }
    console.log(quizId);

    console.log(props.user);
    setUser(props.user);
    setQid(props.qid)
    setOpenSocialPrompt(props.openSocialPrompt)
    console.log(props);
  }, [props]);

  return (
    <>
{openSocialPrompt ? (
<Screen>
  <MakeSocial meetableId={props.meetableId}/>
</Screen>
) : (
  <div
      className="flexbox column fullheight fullwidth mainfont"
      style={{
        // width: "100vw",
        backgroundImage: `url(${background})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        justifyContent: "center",
      }}
    >
      {" "}
      <div
        className="flexbox column"
        style={{
          backgroundColor: "rgba(255,255,255,.8)",
          justifyContent: "space-evenly",
          height: "100%",
          alignItems: "center",
        }}
      >
        <div
          className="flexbox column"
          style={{ fontSize: "4em", flexBasis: 3,marginTop:32 }}
        >
          Meetable
        </div>
        <div className=" lightfont" style={{ fontSize: "2em" }}>
          The social network for{" "}
          <text className="mainfont">{" first years."} </text>
        </div>
        {!user ? (
          // <LoginButton style={{ width: "40%" }} />
          <>
          <StyledButton style={{width:"60%"}} onClick={() => {
            history.push('/quiz')
          }}>
            Start the quiz
          </StyledButton>
          <div>
          <p>Already have an account?</p>
          <LoginButton style={{background:"orange"}}/>

          </div>
          </>
        ) : (
          <>

          <FlexDiv style={{marginTop:48}} vert>
          <FlexDiv vert>
            <div>
          {user.name && user.name}

            </div>
            <div>

          {user.email && user.email}
            </div>
            <div>

          {qid && qid}
            </div>
          {user.avatar && (
            <img src={user.avatar}/>
          )}
          </FlexDiv>
          <FlexDiv>

            <StyledButton
              onClick={() => {
                history.push("/groups/"+props.meetableId);
              }}
            >
              My groups
            </StyledButton>
            <StyledButton
              style={{ background: "orange", marginLeft:16 }}
              onClick={() => {
                history.push({pathname: '/profile/'+props.meetableId, state: {meetableId: props.meetableId, quizId: props.qid}});
              }}
            >
              My profile
            </StyledButton>
          </FlexDiv>
          </FlexDiv>
          </>
        )}
      </div>
    </div>
  
)}

    </>
  );
};

const muiStyles = createStyles({
  root: {
    "& label.Mui-focused": {
      color: "black",
      fontStyle: "normal !important",
    },
    // "& .MuiInput-underline:after": {
    //   borderBottomColor: "yellow",
    // },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        borderColor: "black",
      },
      "&:hover fieldset": {
        borderColor: "black",
      },
      "&.Mui-focused fieldset": {
        borderColor: "black",
      },
    },
  },
  textField: {
    // marginTop: "10px",
    width: "33%",
    alignSelf: "center",
    backgroundColor: "rgba(254,172,0, .3)",
  },
});
export default withStyles(muiStyles)(HomePage);
