            marginTop: "10px",
            marginBottom: "10px",
            fontFamily: "Raleway",
            fontStyle: "normal",
            fontWeight: "200",
            fontSize: "25px",
            lineHeight: "32px",
            textAlign: "center",
            textTransform: "capitalize",
