import React, { useEffect, useState } from "react";
import "./styles/App.css";
import LogoutButton from "./components/LogoutButton";
import { combineReducers } from "redux";
import { sessionReducer } from "redux-react-session";
import { createStore } from "redux";
import { sessionService } from "redux-react-session";
import Quiz from "./components/Quiz";
import Cookies from "js-cookie";
import Authorize from "./components/Authorize";
import HomePage from "./components/HomePage";
import MeetableBar from "./components/MeetableBar";
import GroupsPage from "./components/GroupsPage";
import { useAuth0 } from "@auth0/auth0-react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
  useHistory,
} from "react-router-dom";
import Profile from "./components/Profile";
import FlexDiv from "./design-system/FlexDiv";
import MakeSocial from "./components/MakeSocial";
import Group from "./components/Group";
import { BASE_URL } from "./utils/api";

const reducers = {
  // ... your other reducers here ...
  session: sessionReducer,
};
const reducer = combineReducers(reducers);
const store = createStore(reducer);
sessionService.initSessionService(store);

const options = {
  refreshOnCheckAuth: false,
  redirectPath: "/",
  driver: "COOKIES",
};
sessionService.initSessionService(store, options);
function App() {
  const [qid, setQid] = useState()
  const [meetableId, setMeetableId] = useState()
  let history = useHistory()
  const [openSocialPrompt, setOpenSocialPrompt] = useState(false)
  const { isAuthenticated, loading, getAccessTokenSilently, user } = useAuth0();
  useEffect(()=>{
    const qid = Cookies.get("quizId")
    console.log(qid);
    setQid(qid)
  })
  useEffect(() => {
    const checkLoggedIn = async () => {
      if (isAuthenticated) {
        if(!Cookies.get("quizId")) {
          history.push("/quiz")
          return
        }
        console.log("Checking if logged in");
        /* Send User Data to Server*/

        let userInfo = {
          name: user.nickname || user.name,
          email: user.email,
          profileImage: user.picture,
          authid: user.sub,
        };
        let res = await fetch(`${BASE_URL}/api/users/test`, {
          method: "post",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ ...userInfo }),
        }).catch((e) => console.log(e));

        let r = await res.json();
        Cookies.set("userID", user.sub);
        Cookies.set("meetableID", r.id);
        Cookies.set("userPhoto", user.picture);
        

        console.log(r);
        if(r.msg === "Meetable user exists additional steps required"){
            setOpenSocialPrompt(true)
          }
          setMeetableId(r.id)
       
  
      }
    };
    checkLoggedIn();
    
  }, [loading, getAccessTokenSilently, isAuthenticated, user]);

  return (
    <div className="App flexbox column">
      <FlexDiv justify="flex-end">
        {user && (
          <LogoutButton/>

        )}
      </FlexDiv>
 
      <Router basename="/">
        <Switch>
          <Route path="/" exact>
            <HomePage user={user} qid={qid} openSocialPrompt={openSocialPrompt} meetableId={meetableId}/>
            </Route>
          <Route path="/quiz" exact>
            <Quiz user={user}/>
            </Route>
          <Route path="/profile/:id" exact component={MakeSocial}/>
          <Route path="/groups/:id" exact component={GroupsPage}/>
          <Route path="/group/:id" exact component={Group}/>
        </Switch>
    
      </Router>
      
      {/* {Cookies.get("userID") ? <Quiz /> : <HomePage />} */}
    </div>
  );
}

export default App;
