import React, { useEffect } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import Cookies from "js-cookie";
import { BASE_URL } from "../utils/api";
function Authorize(props) {
  const { isAuthenticated, loading, getAccessTokenSilently, user } = useAuth0();

  useEffect(() => {
    const checkLoggedIn = async () => {
      if (isAuthenticated && !Cookies.get("userID")) {
        /* Send User Data to Server*/

        let userInfo = {
          name: user.nickname || user.name,
          email: user.email,
          profileImage: user.picture,
          authid: user.sub,
        };
        let res = await fetch(`${BASE_URL}/api/users/test`, {
          method: "post",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ ...userInfo }),
        }).catch((e) => console.log(e));

        let uid = await res.json();
        Cookies.set("userID", user.sub);
        Cookies.set("user", uid);
        Cookies.set("userPhoto", user.picture);

        console.log(uid);

        setTimeout(() => {
          if (!loading) window.location.pathname = "/";
        }, 1000);
      }
    };
    if (!loading) {
      checkLoggedIn();
    }
  }, [loading, getAccessTokenSilently, isAuthenticated, user]);
  return <div></div>;
}

export default Authorize;
