import React, { Component } from "react";
import { withStyles, createStyles } from "@material-ui/core";
import Profile from "./Profile";
import profilePic1 from "../assets/profilePic1.jpg";
import profilePic2 from "../assets/profilePic2.jpg";
import profilePic3 from "../assets/profilePic3.jpg";
import { useEffect } from "react";
import { useState } from "react";
import Screen from "./Screen";
import { BASE_URL } from "../utils/api";
const Group = (props) => {
  const [members, setMembers] = useState([]);
  useEffect(() => {
    const groupId = props.match.params.id;
    const f = async () => {
      let res = await fetch(
        `${BASE_URL}/api/groups/byId/${groupId}`,
        {
          method: "get",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      const g = await res.json();
      console.log(g);
      setMembers(g.members);
    };

    f();
  });
  return (
    <Screen>
      <div
        style={{
          height: "100%",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          overflow: "auto",
        }}
        className="groupsPage"
      >
        <h2>Your groupmates</h2>
        <p>Say hello!</p>
        {members && members.length &&
          members.map((member, i) => {
            return (
              <Profile
                index={0}
                picture={member.avatar}
                text={member.blurb}
                name={member.name}
                instagram={member.instagram}
                facebook={member.facebook}
                snapchat={member.snapchat}
              />
            );
          })}
      </div>
    </Screen>
  );
};

const muiStyles = createStyles({});
export default withStyles(muiStyles)(Group);
