import React, { Component } from "react";
import { Typography } from "@material-ui/core";
import { withStyles, createStyles, Chip } from "@material-ui/core";
import circle from "../assets/circle.svg";
import circle2 from "../assets/circle2.svg";
import "../styles/index.css";
const colors = {
  0: "#7ED1EF",
  1: "#F5A159",
  2: "#FADAC5",
  3: "#FFE083",
};

class GroupBubble extends Component {
  renderTags = () => {
    let tags = this.props.tags;
    let tagArray = [];
    const listStyle = {
      fontSize: ".6em",
      maxHeight: "20px",
      textAlign: "center",
    };
    tags.forEach((string, index) => {
      const StyledChip = withStyles({
        root: {
          backgroundColor: "#ffffff",
          color: "black",
          fontFamily: "Raleway",
          maxHeight: "20px",
        },
      })(Chip);
      tagArray.push(
        <li style={listStyle}>
          <StyledChip color="primary" size="small" label={`#${string}`} />
        </li>
      );
    });

    return (
      <ul
        style={{
          paddingLeft: "0px",
          listStyleType: "none",
          justifySelf: "center",
          width: "100%",
          display: "flex",
          justifyContent: "space-evenly",
          maxHeight: "20px",
          alignItems: "flex-start",
        }}
      >
        {tagArray}
      </ul>
    );
  };
  renderImages = () => {
    const members = this.props.members
    const images = members && members.map((u)=>{
      if(u.avatar){
        return u.avatar
      } else {
        return circle
      }
    })
    // let images = this.props.images?.slice(0, 4) || [
    //   circle,
    //   circle2,
    //   circle,
    //   circle2,
    // ];
    let imageArray = [];
    const iconStyle = {
      padding: 0,
      margin: 0,
      position: "absolute",
      bottom: "0px",
    };
    /**
     * God this is so hardcoded it hurts
     */
    images.forEach((src, index) => {
      let num = (index + 1) * 20;
      imageArray.push(
        <li
          style={{
            ...iconStyle,
            right: 20 * images.length + images.length * 20 - 2 * num, //yuck!!!!!
          }}
        >
          <div
            style={{
              backgroundImage: `url(${src})`,
              backgroundSize: "cover",
              borderRadius: "50%",
              border: "1px solid black",
              width: "50px",
              height: "50px",
              backgroundRepeat: "no-repeat",
              backgroundPosition: "center top",
              content: "''",
              willChange: "transform",
              zIndex: "-1",
            }}
          ></div>
        </li>
      );
    });
    return (
      <ul
        style={{
          width: "25%",
          listStyleType: "none",
          display: "flex",
          maxHeight: "50px",
          alignSelf: "flex-end",
          justifySelf: "flex-end",
          paddingBottom: "10px",
          marginBottom: "5px",
          position: "relative",
        }}
      >
        {imageArray}
      </ul>
    );
  };

  render() {
    const Title = withStyles({
      root: {
        fontFamily: "Raleway",
        fontSize: "1.4em",
      },
    })(Typography);
    return (
      <button
        style={{
          ...this.props.style,
          marginTop: "10px",
          borderRadius: "10px",
          boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
          background: `linear-gradient(180deg, rgba(255, 255, 255, 0.55) 0%, rgba(255, 255, 255, 0) 100%), ${
            colors[this.props.index % 4]
          }`,
          minHeight: "150px",
          height: "16.4%",
          width: "83.6%",
          fontFamily: "Raleway",
        }}
        className="groupBubble"
      >
        <div
          style={{
            height: "100%",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
            cursor: "pointer",
          }}
          onClick={this.props.onClick}
        >
          <div>
            <Title>{this.props.title}</Title>
            {this.renderTags()}
          </div>
          <div
            style={{
              height: "100%",
              display: "flex",
              flexDirection: "column",
              alignItems: "flex-end",
              justifyContent: "flex-end",
            }}
          >
            {this.renderImages()}
          </div>
        </div>
      </button>
    );
  }
}

const muiStyles = createStyles({});
export default withStyles(muiStyles)(GroupBubble);
